import React from 'react'
import {ProductsContainer, 
    ProductsHeading, 
    ProductWrapper,
    ProductCard,
    ProductInfo,
    TextWrap,
    Button,
    ProductTitle,
    ProductImg, TripButton, 
    TripBtn
    } from './TripsElements'
    import Bali from "../../images/Bali.jpg";
    import Ta誰wan from "../../images/Ta誰wan.jpg";
    import Duba誰 from "../../images/Duba誰.jpg";
    import Californie from "../../images/Californie.jpg";
    
import { ImLocation } from "react-icons/im"

const Trips = () => {
    return (
        <>
           <ProductsContainer>
            <ProductsHeading>Derniers lieux visit辿</ProductsHeading>    
            <ProductWrapper>
                <ProductCard>
                    <ProductImg src={Bali} alt="Bali" />
                    <ProductInfo>
                        <TextWrap>
                            <ImLocation />
                            <ProductTitle>Bali </ProductTitle>
                        </TextWrap>
                        <Button to='/Maps'>Voir sur la Map</Button>
                    </ProductInfo>
                </ProductCard>

                <ProductCard>
                    <ProductImg src={Ta誰wan} alt="Ta誰wan" />
                    <ProductInfo>
                        <TextWrap>
                            <ImLocation />
                            <ProductTitle>Ta誰wan </ProductTitle>
                        </TextWrap>
                        <Button to='/Maps'>Voir sur la Map</Button>
                    </ProductInfo>
                </ProductCard>

                <ProductCard>
                    <ProductImg src={Duba誰} alt="Duba誰" />
                    <ProductInfo>
                        <TextWrap>
                            <ImLocation />
                            <ProductTitle>Duba誰 </ProductTitle>
                        </TextWrap>
                        <Button to='/Maps'>Voir sur la Map</Button>
                    </ProductInfo>
                </ProductCard>

                <ProductCard>
                    <ProductImg src={Californie} alt="Californie" />
                    <ProductInfo>
                        <TextWrap>
                            <ImLocation />
                            <ProductTitle>Californie </ProductTitle>
                        </TextWrap>
                        <Button to='/Maps'>Voir sur la Map</Button>
                    </ProductInfo>
                </ProductCard>
            </ProductWrapper>
            <TripBtn>
            <TripButton  to='/Trips'>Toutes nos destinations </TripButton>
            </TripBtn>
           </ProductsContainer> 
        </>
    )
}

export default Trips
